@import "./library/all.min.css";
@import "./library/bootstrap";
@import "./fonts";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

html,
body {
  font-family: "Roboto", sans-serif;
}

.focus-none {
  box-shadow: none !important;
  outline: none !important;
}

/* Global Start */

.fw-500 {
  font-weight: 500;
}

.title {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
}

.filter {
  background: rgba(57, 45, 125, 0.15);
  padding: 30px 60px;
}

.placeholder-grey::placeholder {
  color: #C4C4C4;
}

.submit-btn {
  background: #392D7D;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  color: #FFFFFF;
  border-radius: unset;
  width: 170px;
  height: 50px;
}

.long-search {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  text-decoration-line: underline;
  text-transform: uppercase;
  color: #392D7D;
  margin-right: 20px;
}

.bg-392D7D {
  background: #392D7D;
}

.bg-6457B1 {
  background: #6457B1;
}

.btn.bg-392D7D.active {
  background: #6457B1;
}

.dropdown-toggle {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 25px;
  text-align: justify;
  letter-spacing: 0.05em;
  color: #FFFFFF;
}

.dropdown-toggle::after {
  content: unset;
}

.dropdown-menu {
  border: 1px solid #6457B1;
}

.dropdown-item {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.05em;
  color: #000000;
}

.down-transform {
  transform: rotate(90deg);
}

.item-half {
  background: #FFFFFF;
  box-shadow: 5px 5px 5px rgba(57, 45, 125, 0.25);
  padding: 30px;

  &_title {
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: #000000;
  }

  &_text {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.02em;
    color: #000000;
  }
}

.item-full {
  background: #FDFDFD;
  border: 1px solid rgba(57, 45, 125, 0.7);
  box-sizing: border-box;
  padding: 10px 30px;
  transition: all 0.5s ease-in-out;
  cursor: pointer;

  &:hover {
    filter: drop-shadow(5px 5px 5px rgba(57, 45, 125, 0.25));
    margin-bottom: 20px;
    margin-top: 10px;
  }

  &_title {
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: #000000;
  }

  &_text {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.02em;
    color: #000000;
  }
}

.pagination {
  .page-item:first-child .page-link,
  .page-item:last-child .page-link, {
    color: white;
    background: #392D7D;
    border-color: #392D7D;
    border-radius: unset;
  }


  .page-link {
    border-color: transparent;
    border-radius: unset;
    font-family: "Roboto", sans-serif;
    font-weight: bold;
    font-size: 16px;
    line-height: 25px;
    text-align: justify;
    letter-spacing: 0.02em;
    color: #000000;
  }

  .page-item.active .page-link {
    z-index: 3;
    color: #fff;
    background-color: #6457B1;
    border-color: #6457B1;
  }
}

.custom-modal {
  .modal-content {
    border-radius: unset;
    border: unset;
    background: #E1DFEB;
  }
}

.shadow-blue {
  box-shadow: 5px 5px 15px rgba(16, 147, 137, 0.15);
}

.resize-none {
  resize: none;
}

/* Global End */

/* Header Start */

.header {


  &-logo {
    display: flex;
    align-items: center;
    max-width: 380px;
    white-space: normal;

    @include media(567) {
      max-width: 80%;
      margin-right: 0;
    }


    &_img {
      margin-right: 5px;
      @include media(567) {
        margin-right: 0;
        width: 50px;
      }
    }

    &_text {
      font-family: "Roboto", sans-serif;
      font-weight: 500;
      font-size: 18px;
      line-height: 21px;
      text-transform: uppercase;
      color: #000000;
      margin-bottom: 0;

      @include media(567) {
        font-size: 12px;
        line-height: 17px;
      }

      @include media(320) {
        font-size: 11px;
        line-height: 17px;
      }
    }
  }
}

.navbar-light .navbar-nav .nav-link {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}

.navbar-light .navbar-nav .show > .nav-link, .navbar-light .navbar-nav .nav-link.active {
  border-bottom: 1px solid #392D7D;
}

.navbar {
  border-bottom: 2px solid #392D7D;
}

/* Header End */

/* Footer Start */
.footer {
  margin-top: 80px;
  background: #392D7D;
  padding: 12px 0;

  &-text {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    margin-bottom: 0;
    color: #FFFFFF;
  }
}

/* Footer End */

/* Document Start */

.document {
  padding: 30px;

  &-text {
    list-style: inside;
  }
}

.details {
  &[open] {
    .details-summary i {
      transform: rotate(45deg);
    }
  }

  &-summary {
    background: rgba(57, 45, 125, 0.15);
    padding: 12px 24px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    letter-spacing: 0.02em;
    color: #000000;
  }

  &-body {
    border: 1px solid #746BA3;
    padding: 30px;

    &_text {
      color: #000000;
      margin-bottom: 0;
    }
  }
}

.information {
  padding: 20px;
  background: rgba(57, 45, 125, 0.15);
  color: #000;

  &-item {
    padding: 10px;
    background: #fff;
  }

  &-notice {
    margin-top: 18px;
    padding: 10px;
    background: #fff;
  }
}


/* Document End */

/* Contact Start */

.contact {
  &-right {
    padding: 30px;
    background: url("../images/contact-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &_title {
      font-weight: bold;
      font-size: 36px;
      line-height: 42px;
      color: #FFFFFF;
    }

    &_text {
      font-size: 18px;
      line-height: 21px;
      color: #FFFFFF;
      margin-bottom: 0;
    }

    &_icon {
      font-size: 18px;
    }
  }

  &-left {
    padding: 30px;
    background: rgba(57, 45, 125, 0.15);

    &_title {
      max-width: 40%;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      text-transform: uppercase;
      color: #000000;
      @include media(567) {
        max-width: 100%;
      }
    }
  }

  &-form {
    padding: 20px 40px;

    @include media(768) {
      padding: 15px 30px;
    }

    @include media(576) {
      padding: 10px 20px;
    }
  }
}

/* Contact End */

